import * as React from 'react';
import { actionButtons } from '../helper/types';
import { HomeStats } from './Home';

export interface KeypadProps {
  handleKeypress: (digit: string) => void;
  homeState: HomeStats;
}

export interface KeypadState {}

export class Keypad extends React.Component<KeypadProps, KeypadState> {
  state = {};
  render() {
    return (
      <div className="d-flex flex-row justify-content-center">
        <div className="d-flex flex-column justify-content-start">
          <div className="d-flex flex-row">
            <div className="mx-1 p-1">
              <button className="digit" onClick={() => this.props.handleKeypress('1')}>
                1<div className="sub"> </div>
              </button>
            </div>
            <div className="mx-1 p-1">
              <button className="digit" onClick={() => this.props.handleKeypress('2')}>
                2<div className="sub">ABC</div>
              </button>
            </div>
            <div className="mx-1 p-1">
              <button className="digit" onClick={() => this.props.handleKeypress('3')}>
                3<div className="sub">DEF</div>
              </button>
            </div>
            <div className="m-1 p-1">
              <button
                className="round_button"
                disabled={this.props.homeState.calls.length === 0}
                onClick={() => this.props.handleKeypress(actionButtons.SWAP)}
              >
                <i className="bi bi-arrow-repeat" />
              </button>
            </div>
            <div className="m-1 p-1">
              <button
                className={'round_button'}
                onClick={() => this.props.handleKeypress(actionButtons.MUTE)}
                style={{ backgroundColor: this.props.homeState.isMute ? 'red' : '' }}
              >
                <i
                  className={
                    !this.props.homeState.isMute ? 'bi bi-mic-mute-fill' : 'bi bi-mic-fill'
                  }
                />
              </button>
            </div>
          </div>
          <div className="d-flex flex-row">
            <div className="mx-1 p-1">
              <button className="digit" onClick={() => this.props.handleKeypress('4')}>
                4<div className="sub">GHI</div>
              </button>
            </div>
            <div className="mx-1 p-1">
              <button className="digit" onClick={() => this.props.handleKeypress('5')}>
                5<div className="sub">JKL</div>
              </button>
            </div>
            <div className="mx-1 p-1">
              <button className="digit" onClick={() => this.props.handleKeypress('6')}>
                6<div className="sub">MNO</div>
              </button>
            </div>
            <div className="m-1 p-1">
              <button
                className="round_button"
                disabled={this.props.homeState.calls.length !== 2}
                style={{}}
                onClick={() => this.props.handleKeypress(actionButtons.CONFERENCE)}
              >
                <i className="bi bi-people-fill" />
              </button>
            </div>
            <div className="m-1 p-1">
              <button
                className="round_button"
                disabled={this.props.homeState.calls.length === 0}
                style={{
                  backgroundColor: this.props.homeState.transferInitiated ? 'greenyellow' : '',
                }}
                onClick={() => this.props.handleKeypress(actionButtons.TRANSFER)}
              >
                <i className="bi bi-telephone-forward-fill"></i>
              </button>
            </div>
          </div>
          <div className="d-flex flex-row">
            <div className="mx-1 p-1">
              <button className="digit" onClick={() => this.props.handleKeypress('7')}>
                7<div className="sub">PQRS</div>
              </button>
            </div>
            <div className="mx-1 p-1">
              <button className="digit" onClick={() => this.props.handleKeypress('8')}>
                8<div className="sub">TUV</div>
              </button>
            </div>
            <div className="mx-1 p-1">
              <button className="digit" onClick={() => this.props.handleKeypress('9')}>
                9<div className="sub">WXYZ</div>
              </button>
            </div>
            <div className="m-1 p-1">
              <button
                className="round_button"
                // disabled={!this.props.homeState.activeCall}
                style={{}}
                onClick={() => this.props.handleKeypress('hold')}
              >
                <i className="bi bi-pause-fill" />
              </button>
            </div>
            <div className="m-1 p-1">
              <button
                className="round_button"
                //disabled={!this.props.connected}
                style={{}}
                onClick={() => this.props.handleKeypress('swap')}
              >
                <i className="bi bi-telephone-plus-fill" />
              </button>
            </div>
          </div>
          <div className="d-flex flex-row">
            <div className="mx-1 p-1">
              <button
                className="digit justify-center"
                onClick={() => this.props.handleKeypress('*')}
              >
                *
              </button>
            </div>
            <div className="mx-1 p-1">
              <button
                className="digit justify-center"
                onClick={() => this.props.handleKeypress('0')}
              >
                0
              </button>
            </div>
            <div className="mx-1 p-1">
              <button
                className="digit justify-center"
                onClick={() => this.props.handleKeypress('#')}
              >
                #
              </button>
            </div>
            <div className="m-1 p-1">
              {' '}
              <button
                className="round_button"
                // disabled={!this.props.homeState.activeCall}
                style={{}}
                onClick={() => this.props.handleKeypress(actionButtons.SETTINGS)}
              >
                <i className="bi bi-gear-fill" />
              </button>
            </div>
            <div className="m-1 p-1">
              {' '}
              <button
                className="round_button"
                onClick={() => this.props.handleKeypress(actionButtons.EXIT)}
              >
                <i className="bi bi-house-fill" />
              </button>
            </div>
          </div>
          <div className="d-flex flex-row">
            <div className="mx-1 p-1">
              <button className="round_button" onClick={() => this.props.handleKeypress('e')}>
                <i className={'bi bi-backspace-fill'} />
              </button>
            </div>
            <div className="mx-1 p-1">
              <button
                className="round_button"
                style={{ backgroundColor: 'green', width: '150px', borderRadius: '40px' }}
                onClick={() => this.props.handleKeypress(actionButtons.CALL)}
              >
                <i className={'bi bi-telephone-fill'} />
              </button>
            </div>
            <div className="mx-auto py-1">
              <button
                className="round_button"
                //disabled={!this.props.connected}
                style={{
                  width: '150px',
                  borderRadius: '40px',
                  backgroundColor: '#ff0000',
                }}
                onClick={() => this.props.handleKeypress(actionButtons.END)}
              >
                <i
                  className="bi bi-telephone-fill"
                  style={{ transform: 'rotate(135deg) scale(1.8)' }}
                />
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
