import * as React from 'react';

import { Registerer, UserAgent, UserAgentOptions, RegistererState } from 'sip.js';
import { ServerDetails } from '../helper/types';

//#region Helpers
enum RegistrationStates {
  IDEAL = 'IDEAL',
  REGISTERING = 'REGISTERING',
  REGISTERED = 'REGISTERED',
}

//#endregion

export interface LoginProps {
  handleRegistered: (UA: UserAgent, server: ServerDetails) => void;
}
export interface LoginStats {
  server: ServerDetails;
  hasLoginDetails: boolean;
  regState: string;
  loginError: string;
}

export class Login extends React.Component<LoginProps, LoginStats> {
  state = {
    server: { name: '', user: '', password: '', server: '', port: 7443 },
    hasLoginDetails: false,
    regState: RegistrationStates.IDEAL,
    loginError: '',
  };
  // Check if username and password is supplied by server
  componentDidMount(): void {
    this.setState(
      {
        ...this.state,
        server: {
          name:
            (document.getElementById('name') as HTMLInputElement).value ||
            (document.getElementById('username') as HTMLInputElement).value,
          user: (document.getElementById('username') as HTMLInputElement).value,
          password: (document.getElementById('password') as HTMLInputElement).value,
          server: (document.getElementById('server') as HTMLInputElement).value,
          port: parseInt((document.getElementById('port') as HTMLInputElement).value),
        },
      },
      () => {
        //If all the data is correct start registration or show the form otherwise.
        if (
          this.state.server.user === '' ||
          this.state.server.password === '' ||
          this.state.server.server === ''
        ) {
          this.setState({ hasLoginDetails: false });
        } else {
          this.setState({ hasLoginDetails: true });
          this.handleRegistrationClick();
        }
      },
    );
  }
  //Handle Input at the form
  handleInputs = (e: any) => {
    var { value, name } = e.target;
    this.setState({
      ...this.state,
      server: {
        ...this.state.server,
        [name]: value,
      },
    });
  };
  // Registration

  handleRegistrationClick = () => {
    this.setState({ regState: RegistrationStates.REGISTERING });
    const uri = UserAgent.makeURI(`sip:${this.state.server.user}@${this.state.server.server}`);
    const transportOptions = {
      server: `wss://${this.state.server.server}:${this.state.server.port}`,
    };
    const userAgentOptions: UserAgentOptions = {
      uri: uri,
      transportOptions,
      authorizationUsername: this.state.server.user,
      authorizationPassword: this.state.server.password,
      displayName: this.state.server.user,
      sendInitialProvisionalResponse: true,
      logBuiltinEnabled: false,
      userAgentString: 'SIP.js/0.7.8 SaraPhone 04',
      sessionDescriptionHandlerFactoryOptions: { iceCheckingTimeout: 500 },
      logLevel: 'debug',
    };
    const userAgent = new UserAgent(userAgentOptions);

    const registererOptions: any = {
      expires: 1000,
      refreshFrequency: 80,
      onReject: (Response) => {
        userAgent.stop();
        this.setState({
          loginError:
            Response.message.reasonPhrase === 'Forbidden'
              ? 'Username or password incorrect'
              : Response.message.reasonPhrase,
          hasLoginDetails: false,
          regState: RegistrationStates.IDEAL,
        });
      },
    };
    const registerer = new Registerer(userAgent, registererOptions);

    registerer.stateChange.addListener((state: RegistererState) => {
      if (state === RegistererState.Registered) {
        this.props.handleRegistered(userAgent, this.state.server);
      }
    });
    userAgent.start().then(() => {
      registerer.register();
    });
  };

  render() {
    return (
      <div className="bg-light min-vh-100 d-flex flex-row align-items-center dark:bg-transparent">
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-lg-8">
              <div className="card-group d-block d-md-flex row">
                <div className="card col-md-7 p-4 mb-0">
                  <div className="card-body">
                    <div id="signin" className={'d-flex flex-column'} style={{ height: '100%' }}>
                      <div className="row text-center mx-auto my-auto" style={{ width: '80%' }}>
                        <span className="w-100 mb-5">
                          <img
                            src="./img/logo.png"
                            alt="Logo"
                            className="mx-auto"
                            style={{ width: '150px', height: 'auto' }}
                          />
                        </span>
                        <form className={this.state.hasLoginDetails ? 'd-none' : 'd-block'}>
                          <input
                            type="text"
                            placeholder="Username"
                            name="user"
                            autoComplete="off"
                            className="form-control my-2"
                            value={this.state.server.user}
                            onChange={this.handleInputs}
                          />
                          <input
                            type="password"
                            placeholder="Password"
                            name="password"
                            autoComplete="off"
                            className="form-control my-2"
                            value={this.state.server.password}
                            onChange={this.handleInputs}
                          />
                          <input
                            type="text"
                            placeholder="Server"
                            name="server"
                            autoComplete="off"
                            className="form-control my-2"
                            value={this.state.server.server}
                            onChange={this.handleInputs}
                          />
                          <input
                            type="text"
                            placeholder="port"
                            name="port"
                            autoComplete="off"
                            className="form-control my-2"
                            value={this.state.server.port}
                            onChange={this.handleInputs}
                          />
                        </form>
                        <div id="login-button">
                          <button
                            className="btn btn-primary form-control"
                            id="loginbtn"
                            type="button"
                            onClick={this.handleRegistrationClick}
                            disabled={this.state.regState === RegistrationStates.REGISTERING}
                          >
                            <div
                              style={{
                                pointerEvents: 'none',
                                display:
                                  this.state.regState === RegistrationStates.REGISTERING
                                    ? 'none'
                                    : 'block',
                              }}
                            >
                              Connect
                            </div>
                            <div
                              style={{ pointerEvents: 'none' }}
                              className={
                                this.state.regState === RegistrationStates.REGISTERING
                                  ? ''
                                  : 'd-none'
                              }
                            >
                              <span
                                className="spinner-border spinner-border-sm"
                                role="status"
                                aria-hidden="true"
                              />
                              <span className="sr-only ms-2">Registering in please wait...</span>
                            </div>
                          </button>

                          <span
                            id="loginErrorMessage"
                            className={
                              this.state.loginError !== ''
                                ? 'mt-2 badge bg-danger'
                                : 'mt-2 badge bg-danger d-none'
                            }
                          >
                            {this.state.loginError}
                          </span>
                          <button
                            className="btn btn-danger form-control my-2"
                            onClick={() => {
                              window.location.replace('/');
                            }}
                          >
                            Back to fusionpbx
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
