import * as React from 'react';
import { actionButtons } from '../helper/types';
import { HomeStats } from './Home';
export interface SettingsDialogProps {
  handleButtonPress: (action: string) => void;
  settingChangers: {
    mic: (value: string) => void,
    touchTones: (value: boolean) => void,
    ringer: (value: string) => void,  }
  homeState: HomeStats;
}

export interface SettingsDialogState {}

export class SettingsDialog extends React.Component<SettingsDialogProps, SettingsDialogState> {
  handleSelectChange = (event) => {
    console.log(event)
   }
  render() {
    if (!!this.props.homeState.showSettings) {
      return (
        <div
          className="d-block position-absolute bg-light border p-2 mt-4"
          style={{ zIndex: 99, width: '454px' }}
        >
          <div className="form-group my-2">
            <label htmlFor="mic">Default Mic</label>
            <select
              className="form-select"
              id="mic"
              value={this.props.homeState.settings.mic}
              onChange={(e) => {
                this.props.settingChangers.mic(e.target.value);
              }}
            >
              {this.props.homeState.mediaDevices.map((m) => {
                return (
                  <option key={m.deviceId} value={m.deviceId}>
                    {m.name}
                  </option>
                );
              })}
            </select>
          </div>
          <div className="form-group my-2">
            <label htmlFor="ringer">ringtone</label>
            <select
              className="form-select"
              id="ringer"
              value={this.props.homeState.settings.ringer}
              onChange={(e) => {
                this.props.settingChangers.ringer(e.target.value);
              }}
            >
              {[ 'ring1', 'ring2', 'ring3', 'ring4', 'ring5'].map((m) => {
                return (
                  <option key={m} value={m}>
                    {m.toLocaleUpperCase()}
                  </option>
                );
              })}
            </select>
          </div>
          <div className="form-group">
            <div className="form-check">
              <input className="form-check-input" type="checkbox" id="gridCheck" checked={this.props.homeState.settings.touchTones} onChange={(e)=>this.props.settingChangers.touchTones(e.target.checked)} />
              <label className="form-check-label" htmlFor="gridCheck">
               Touch Tones
              </label>
            </div>
          </div>
          <div className="form-group my-2">
            <button
              className="btn btn-primary max-auto"
              onClick={() => this.props.handleButtonPress(actionButtons.SETTINGS)}
            >
              Save
            </button>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }
}
