import * as React from 'react';
import { ICall } from './Home';

interface CallInfoProps {
  call: ICall;
  isActive: boolean;
}

interface CallInfoState {}

class CallInfo extends React.Component<CallInfoProps, CallInfoState> {
  state = {};
  render() {
    if (this.props.call) {
      return !this.props.isActive ? (
        <div>
          <div className="d-flex flex-row justify-content-between flex-wrap flex-grow-1">
            <div className="d-flex px-0"></div>
            <div className="px-2">
              <i className={'bi bi-telephone-forward-fill pe-none'} />
            </div>
            <div className="px-2">
              <h5 className="m-0 user-select-none park-number">{this.props.call.number}</h5>
            </div>
            <div className="px-2">
              <small className="m-0 user-select-none park-timer">
                {new Date(this.props.call.duration * 1000).toISOString().substring(11, 19)}
              </small>
            </div>
            <div className="px-2">
              <i
                className={
                  'bi bi-pause-fill pe-none blink ' + (true ? 'text-success' : 'text-danger')
                }
              />
            </div>
            <div className="d-flex px-0">
              {/* <i
              className={'bi bi-play-circle-fill ' + (false ? 'text-success' : 'text-danger')}
              style={{
                transform: 'scale(2)',
              }}
            /> */}
            </div>
          </div>
        </div>
      ) : (
        <div>
          <div className="d-flex flex-column justify-content-center">
            <div className="mx-auto"></div>
            <div className="mx-auto py-2">
              <strong className="m-0 active-number">{this.props.call.number}</strong>
            </div>
            <div className="mx-auto py-2">
              {new Date(this.props.call.duration * 1000).toISOString().substring(11, 19)}
            </div>
          </div>
        </div>
      );
    } else {
      return '';
    }
  }
}
export default CallInfo;
