import React from 'react';
import './css/dialer.css';
import './css/webphone.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import './css/utilities.css';
import { UserAgent, UserAgentState } from 'sip.js';
//Components
import { Login } from './Components/login';
import { Home } from './Components/Home';
import { ServerDetails } from './helper/types';
//------
export interface AppProps {}
export interface AppState {
  server?:ServerDetails
  UA: UserAgent;
  isSignedin: boolean;
}

class App extends React.Component<AppProps, AppState> {
  state = { isSignedin: false, UA: null,server:null };
  handleRegistered = (userAgent: UserAgent, server: ServerDetails) => {
    //listen to useragent disconnect events
    userAgent.stateChange.addListener((state: UserAgentState) => {
      switch (state) {
        case UserAgentState.Started:
          console.log('UA Started.');
          break;
        case UserAgentState.Stopped:
          this.setState({isSignedin:false,UA:null})
          console.log('UA Disconnected.');
          break;
      }
    });
    this.setState({ UA: userAgent, isSignedin: true, server: server }, () => {
      this.state.UA.delegate.onDisconnect = () => { this.setState({isSignedin:false,UA:null})}
    });
  };

  render() {
    switch (this.state.isSignedin) {
      case true:
        return <Home UserAgent={this.state.UA} server={this.state.server} />;
      case false:
        return <Login handleRegistered={this.handleRegistered} />;
    }
  }
}
export default App;
