import React from 'react';
import CallInfo from './CallInfo';
import { UserAgent } from 'sip.js';
import { HomeStats } from './Home';
export interface DisplayProps {
  homeState:HomeStats
  UA: UserAgent;
  handleInput: (event: React.FormEvent<HTMLInputElement>) => void;
}
export interface DisplayStats {}
export class Display extends React.Component<DisplayProps, DisplayStats> {
  state: DisplayStats = {};
  render() {
    return (
      <div id="display" className="d-flex flex-column mb-3">
        <div
          className="row"
          style={{ margin: '10px', alignItems: 'center', backgroundColor: 'white' }}
        >
          <div
            className="col-4 d-flex flex-row"
            style={{
              alignItems: 'center',
            }}
          >
            <i
              id="registration_indicator"
              className={'bi bi-lock-fill ' + (true ? 'text-success' : 'text-danger')}
              style={{
                transform: 'scale(1.5)',
              }}
            />
            <h4 id="registration_user" className="ms-3 mt-0 mb-0">
              {this.props.UA.configuration.authorizationUsername}
            </h4>
          </div>
        </div>
        <hr></hr>
        <div
          style={{
            backgroundColor: 'var(--screen-back-color)',
            borderRadius: '24px',
            height: '220px',
          }}
        >
          <div id="parkedCalls" className="row d-flex flex-row mt-3" style={{ height: '24px' }}>
            <div className="col-12">
              <CallInfo call={this.props.homeState.calls.find(c=>c.isHold === true)} isActive={false} />
            </div>
          </div>
          <div id="activeCall" className="row d-flex flex-row my-2" style={{ height: '104px' }}>
            <div className="col-12">
              <CallInfo call={this.props.homeState.calls.find((c)=>c.isHold ===false)} isActive={true} />
            </div>
          </div>
          <div id="digit-input" className="row d-flex flex-row my-2">
            <div className="d-flex flex-row">
              <div className="d-flex flex-grow-1 mx-1 p-1">
                <input
                  className="form-control shadow-none text-center ms-1"
                  onChange={this.props.handleInput}
                  value={this.props.homeState.dialerNumber}
                  type="text"
                  name="dialerNumber"
                  id="dialerNumber"
                  autoComplete="off"
                  placeholder="_"
                  style={{
                    color: 'var(--screen-text-color)',
                    fontFamily: 'Roboto',
                    fontSize: '1.7rem',
                    backgroundColor: 'var(--back-color)',
                    border: 'none',
                    width: '100%',
                  }}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
